h4 {
  font-weight: bold;
}

.btn-add {
  height: 40px;
  width: 40px;
  font-size: 24px;
  border-radius: 35%;
  border: 1px solid #747474;
  background-color: #fee5af;
  float: right;
}

.country-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #c1d5a0;
  height: 50vh;
  width: 30vw;
  padding: 20px;
}

.bucket-list {
  height: 50vh;
  width: 30vw;
  -webkit-align-self: center;
          align-self: center;
  background-color: #e9c565;
  padding: 20px;
  overflow-y: scroll;
}

h2 {
  text-align: center;
}

.subtitle {
  text-align: center;
  font-style: italic;
  font-size: 12px;
}

li {
  margin-bottom: 15px
}

.btn-remove {
  float: right;
  height: 25px;
  width: 25px;
  font-size: 10px;
  border-radius: 35%;
  border: 1px solid #747474;
  background-color: #f14646;
}

body {
  font-family: 'Quicksand', sans-serif;
  background-color: #e8e8e8
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  height: 100vh;
}

